<template>
  <div class="currentPage">
    <div class="nav">
      <span class="name">{{ $t('title') }}</span>
      <img class="decorate" src="../../assets/decorate.png" v-if="childMenuItems && childMenuItems.length > 0"
        @click="updateMenuExpandState">
      <div class="navMenus">
        <div class="navMenuItem" :class="{'active': item.id == navMenuId}" v-for="item in menus"
          @click="switchNavMenu(item)">{{ menusTitle(item.name) }}</div>
      </div>
      <el-radio-group v-model="tabPosition" @change="change">
        <el-radio-button label="中文">中文</el-radio-button>
        <el-radio-button label="英文">英文</el-radio-button>
      </el-radio-group>
    </div>
    <div class="group">
      <div class="leftGroup" v-if="childMenuItems && childMenuItems.length > 0">
        <div class="leftGroupContent" :class="{'show': menuExpand, 'hide':!menuExpand}">
          <div class="childMenus">
            <div class="childMenuItem" :class="{'active': item.id == childItemId}" v-for="item in childMenuItems"
              @click="switchChildMenu(item)">{{ menusTitle(item.name) }}</div>
          </div>
        </div>
      </div>
      <div class="mainGroup" :class="{'show': menuExpand, 'hide':!menuExpand}">
        <keep-alive :max="10" :include="this.$store.state.keepAliveList">
          <router-view v-if="this.$route.meta.keepAlive" />
        </keep-alive>
        <router-view v-if="!this.$route.meta.keepAlive" />
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from "vue"
  export default {
    data() {
      return {
        tabPosition: '中文',
        activeIndex: '/home.html',
        menuExpand: true,
        navMenuId: null,
        childItemId: null,
        childMenuItems: null,
        menus: [{
            name: '首页',
            path: '/home.html',
            id: '1',
            chirdern: [{
                name: '导航一',
                path: '/home/testPage1.html',
                id: '11'
              },
              {
                name: '导航二',
                path: '/home/testPage2.html',
                id: '12'
              },
              {
                name: '文本差异对比工具',
                path: '/home/textCompare.html',
                id: '13'
              },
              {
                name: '导出excel',
                path: '/home/exportExcel.html',
                id: '14'
              },
              {
                name: 'CSS动画',
                path: '/home/animation.html',
                id: '15'
              },
              {
                name: 'MQTT',
                path: '/home/mqtt.html',
                id: '16'
              }
            ]
          },
          {
            name: '订单',
            path: '/order.html',
            id: '2'
          },
          {
            name: '五子棋',
            path: '/showdoc.html',
            id: '3'
          },
          {
            name: '飞机大战',
            path: '/airplane.html',
            id: '4'
          }
        ]
      }
    },
    mounted() {

      Vue.prototype.$settingMainPage = this.settingMainPage;

      this.initPage();
      window.addEventListener("popstate", () => {
        this.initPage();
      })
    },
    methods: {
      initPage() {
        let pId = this.getUrlParem('p');
        let cId = this.getUrlParem('c');
        if (pId) {
          pId = window.atob(pId);
          this.navMenuId = pId;
          for (var i = 0; i < this.menus.length; i++) {
            if (this.menus[i].id == pId) {
              if (this.menus[i].chirdern && this.menus[i].chirdern.length > 0) {
                this.menuExpand = true;
                this.childMenuItems = this.menus[i].chirdern;
                if (cId) {
                  this.childItemId = window.atob(cId);
                }
              } else {
                this.menuExpand = false;
                this.childMenuItems = [];
              }
            }
          }
        } else {
          this.switchNavMenu(this.menus[0]);
        }
      },
      switchNavMenu(item) {
        this.navMenuId = item.id;
        this.childItemId = null;
        if (item.chirdern && item.chirdern.length > 0) {
          this.menuExpand = true;
          this.childMenuItems = item.chirdern;
          this.switchChildMenu(item.chirdern[0]);
        } else {
          this.menuExpand = false;
          this.childMenuItems = [];
          this.settingMainPage(item.path);
        }
      },
      switchChildMenu(item) {
        if (item.id == this.childItemId) {
          return;
        }
        this.childItemId = item.id;
        this.settingMainPage(item.path);
      },
      settingMainPage(url, isReplace) {
        let replaceStateFun = () => {
          setTimeout(() => {
            let url = location.href;
            if (this.navMenuId) {
              url += (url.indexOf("?") == -1 ? "?" : "&");
              url += "p=" + window.btoa(this.navMenuId);
            }
            if (this.childItemId) {
              url += (url.indexOf("?") == -1 ? "?" : "&");
              url += "c=" + window.btoa(this.childItemId);
            }
            url += (url.indexOf("?") == -1 ? "?" : "&");
            url += "r=" + Math.random();
            history.replaceState({}, document.title, url);
          }, 1);
        };
        if (isReplace) {
          this.$router.replace(url, replaceStateFun, () => {});
        } else {
          this.$router.push(url, replaceStateFun, () => {});
        }
      },
      menusTitle(item) {
        if (this.$te('menus.' + item)) {
          return this.$t('menus.' + item)
        }
        return item;
      },
      change(value) {
        this.$i18n.locale = value == "中文" ? "zh" : "en";
      },
      // 获取地址中参数as
      getUrlParem: function(name) {
        return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(window.location
          .href) || [, ""])[1].replace(/\+/g, '%20')) || null
      },
      // 修改状态
      updateMenuExpandState() {
        this.menuExpand = !this.menuExpand;
      },
    }
  }
</script>

<style>
  .currentPage {
    width: 100%;
    height: 100%;
  }

  .currentPage .group {
    margin: 16px;
    height: calc(100% - 60px - 32px);
  }

  .nav {
    background-color: royalblue;
    height: 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 32px;
    position: relative;
  }

  .nav .name {
    font-size: 18px;
    color: white;
  }

  .nav .decorate {
    position: absolute;
    margin-left: 200px;
    cursor: pointer;
  }

  .nav .navMenus {
    display: flex;
    align-items: center;
    margin-left: 50px;
    height: 100%;
    position: absolute;
    left: 250px;
  }

  .navMenus .navMenuItem {
    font-size: 18px;
    color: white;
    width: 120px;
    height: 100%;
    line-height: 60px;
    text-align: center;
    cursor: pointer;
  }

  .navMenus .active {
    background-color: #ffffff22;
  }

  .leftGroup {
    position: absolute;
    left: 16px;
    top: 76px;
    height: calc(100% - 92px);
    overflow: hidden;
    border-radius: 4px;
  }

  .leftGroup .leftGroupContent {
    height: 100%;
    overflow: hidden;
  }

  /**
     * 菜单 打开收起样式
     */
  .leftGroupContent.show {
    width: 156px;
  }

  .leftGroupContent.hide {
    width: 0;
  }

  .leftGroupContent .childMenus {
    height: 100%;
    background-color: white;
    overflow: hidden;
    padding: 16px;
  }

  .childMenus .childMenuItem {
    height: 44px;
    line-height: 44px;
    text-align: center;
    cursor: pointer;
    border-radius: 4px;
    margin-bottom: 6px;
  }

  .childMenuItem.active,
  .childMenuItem:hover {
    color: white;
    background-color: #4169e1;
  }

  .mainGroup {
    background-color: white;
    height: 100%;
    position: absolute;
    height: calc(100% - 92px);
    border-radius: 4px;
    overflow: hidden;
  }

  .mainGroup.show {
    width: calc(100% - 156px - 48px);
    left: 172px;
    margin-left: 16px;

  }

  .mainGroup.hide {
    width: calc(100% - 32px);
    left: 16px;
    margin-left: 0;
  }
</style>
